import React, { Component } from 'react';
import { RadioGroup, Radio, FormControlLabel, FormControl, TextField } from '@material-ui/core';
import { withStyles } from '@material-ui/styles';
import Button from '@material-ui/core/Button';
import Background from '../images/back_pattern.png'

const useStyles = theme => ({
    root: {
        border: 2,
        borderRadius: 3,
        boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
        height: 56,
        margin: 20,
    },
});

var sectionStyle = {
    overflowY: "hidden",
    overflowX: "hidden",
    height: "100%",
    width: "auto",
    backgroundImage: `url(${Background})`,
};

export class ApplicationForm extends Component {
    state = {
        emailError: "",
        pageError: "",
        postalCodeError: "",
        firstNameError: "",
        lastNameError: "",
        orgNameError: ""
    };

    continue = e => {
        e.preventDefault();

        //Delete later
        console.log(this.props.values);
        //Delete 
        const isValid = this.validateForm();  //comment this out for testing
        //const isValid = true;  //uncomment this for production
        if (isValid) {
            this.props.nextStep();
        }
    };

    validateForm = () => {
        let emailError = "";
        let pageError = "";
        let postalCodeError = "";
        let firstNameError = "";
        let lastNameError = "";
        let orgNameError = "";

        const emailRegex = /^\S+@\S+\.\S+$/;
        const postalCodeRegex = /^[A-Za-z]\d[A-Za-z][ -]?\d[A-Za-z]\d$/;
        const namesRegex = /^[a-zA-Z]+$/;

        if (this.props.values.postalCode == "" || postalCodeRegex.test(this.props.values.postalCode)) {
            postalCodeError = "";
            this.setState({ postalCodeError });
        } else if (!postalCodeRegex.test(this.props.values.postalCode)) {
            postalCodeError = 'Invalid postal code';
        }
        if (!emailRegex.test(this.props.values.email)) {
            emailError = 'Invalid Email';
            pageError = 'You must input an email at minimum.';
        }
        if (this.props.values.organizationName == "" && this.props.values.userType == "organization") {
            console.log(this.props.values.organizationName)
            orgNameError = 'Invalid Organization Name';
            pageError = 'You must input an proper orgnization name.';
        }
        if (!namesRegex.test(this.props.values.firstName)) {
            firstNameError = 'Invalid First Name';
            pageError = 'You must input a first name.';
        }
        if (!namesRegex.test(this.props.values.lastName)) {
            lastNameError = 'Invalid Last Name';
            pageError = 'You must input a last name.';
        }
        if (postalCodeError || emailError || firstNameError || lastNameError || pageError || orgNameError) {
            this.setState({ emailError });
            this.setState({ firstNameError });
            this.setState({ lastNameError });
            this.setState({ pageError });
            this.setState({ orgNameError });
            this.setState({ postalCodeError });

            return false;
        }
        this.props.values.emailError = "";
        this.props.values.firstNameError = "";
        this.props.values.lastNameError = "";
        this.props.values.orgNameError = "";
        this.setState({ emailError });
        this.setState({ firstNameError });
        this.setState({ lastNameError });
        this.setState({ orgNameError });
        return true;
    };

    render() {
        this.isValid = true;
        const { values, handleChange } = this.props;
        const { classes } = this.props;
        let renderNameField;
        if (values.userType == 'organization') {
            renderNameField =
                <div>
                    <TextField
                        id="firstName"
                        placeholder="Primary Contact First Name"
                        label="Primary Contact First Name"
                        variant="outlined"
                        onChange={handleChange('firstName')}
                        defaultValue={values.firstName}
                        error={this.state.firstNameError != ""}
                        inputProps={{ maxLength: 50 }}
                        helperText={this.state.firstNameError}
                        className={classes.root}
                        required
                    />
                    <TextField
                        id="lastName"
                        placeholder="Primary Contact Last Name"
                        label="Primary Contact Last Name"
                        variant="outlined"
                        onChange={handleChange('lastName')}
                        defaultValue={values.lastName}
                        error={this.state.lastNameError != ""}
                        inputProps={{ maxLength: 50 }}
                        helperText={this.state.lastNameError}
                        className={classes.root}
                        required
                    />
                    <TextField
                        id="orgName"
                        placeholder="Organization Name"
                        label="Organization Name"
                        variant="outlined"
                        onChange={handleChange('organizationName')}
                        defaultValue={values.organizationName}
                        error={this.state.orgNameError != ""}
                        inputProps={{ maxLength: 50 }}
                        helperText={this.state.orgNameError}
                        className={classes.root}
                        required
                    />
                </div>;
        } else {
            renderNameField =
                <div>
                    <TextField
                        id="firstName"
                        placeholder="First Name"
                        label="First Name"
                        variant="outlined"
                        onChange={handleChange('firstName')}
                        defaultValue={values.firstName}
                        error={this.state.firstNameError != ""}
                        inputProps={{ maxLength: 50 }}
                        helperText={this.state.firstNameError}
                        className={classes.root}
                        required
                    />
                    <TextField
                        id="lastName"
                        placeholder="Last Name"
                        label="Last Name"
                        variant="outlined"
                        onChange={handleChange('lastName')}
                        defaultValue={values.lastName}
                        error={this.state.lastNameError != ""}
                        inputProps={{ maxLength: 50 }}
                        helperText={this.state.lastNameError}
                        className={classes.root}
                        required
                    />
                </div>;
        }
        return (
            <React.Fragment>
                <section style={sectionStyle}>
                    <div className="applicationFormTitle">
                        <h1>1. Application Information</h1>
                    </div>

                    <div className="formLayout">
                        <div className="numberCircle">1</div>
                        <FormControl component="fieldset">
                            <h3>Are you registering as an individual, or representing an organization?</h3>
                            <br />
                            <RadioGroup value={this.props.values.userType} aria-label="userType" id="userType" name="userType" onChange={handleChange('userType')}>
                                <FormControlLabel value="individual" control={<Radio />} label="Individual" />
                                <FormControlLabel value="organization" control={<Radio />} label="Organization" />
                            </RadioGroup>
                        </FormControl>

                        <div className="numberCircle">2</div>
                        <h3>Please fill out these details</h3>
                        {renderNameField}
                        <div className="numberCircle">3</div>
                        <h3>Address Information</h3>
                        <TextField
                            id="streetAddress"
                            placeholder="Street Address"
                            label="Street Address"
                            variant="outlined"
                            onChange={handleChange('streetAdress')}
                            defaultValue={values.streetAdress}
                            inputProps={{ maxLength: 60 }}
                            className={classes.root}
                        />
                        <TextField
                            id="city"
                            placeholder="Vancouver"
                            label="City"
                            variant="outlined"
                            onChange={handleChange('city')}
                            defaultValue={values.city}
                            inputProps={{ maxLength: 50 }}
                            className={classes.root}
                        />
                        <TextField
                            id="province"
                            placeholder="BC"
                            label="Province/State"
                            variant="outlined"
                            onChange={handleChange('province')}
                            defaultValue={values.province}
                            inputProps={{ maxLength: 40 }}
                            className={classes.root}
                        />
                        <TextField
                            id="country"
                            placeholder="Canada"
                            label="Country"
                            variant="outlined"
                            onChange={handleChange('country')}
                            defaultValue={values.country}
                            inputProps={{ maxLength: 40 }}
                            className={classes.root}
                        />
                        <br />
                        <br />
                        <br />

                        <TextField
                            id="postalCode"
                            placeholder="Postal code"
                            label="Postal Code"
                            variant="outlined"
                            onChange={handleChange('postalCode')}
                            defaultValue={values.postalCode}
                            error={this.state.postalCodeError != ""}
                            inputProps={{ maxLength: 7 }}
                            className={classes.root}
                            helperText={this.state.postalCodeError}
                        />
                        <TextField
                            id="email"
                            placeholder="Email"
                            label="Email Address"
                            variant="outlined"
                            onChange={handleChange('email')}
                            defaultValue={values.email}
                            className={classes.root}
                            error={this.state.emailError != ""}
                            inputProps={{ maxLength: 60 }}
                            helperText={this.state.emailError}
                            required
                        />

                        <TextField
                            id="phone"
                            placeholder="(604) 555-5555"
                            label="Phone Number"
                            variant="outlined"
                            onChange={handleChange('phone')}
                            defaultValue={values.phone}
                            inputProps={{ maxLength: 18 }}
                            className={classes.root}
                        />
                        <TextField
                            id="fax"
                            placeholder="Fax"
                            label="Fax No."
                            variant="outlined"
                            onChange={handleChange('fax')}
                            defaultValue={values.fax}
                            inputProps={{ maxLength: 18 }}
                            className={classes.root}
                        />
                        <br />
                        <br />
                        <br />
                        <br />
                        <div className="validation">{this.state.pageError}</div>
                        <Button size="large" type="submit" color="primary" variant="contained" onClick={this.continue}>Continue</Button>
                    </div>
                </section>
            </React.Fragment>
        );
    }
}
export default withStyles(useStyles)(ApplicationForm);
