import React, { Component } from 'react';
import Button from '@material-ui/core/Button';
import { RadioGroup, Radio, FormControlLabel, TextField } from '@material-ui/core';
import Background from '../images/back_pattern.png'

var sectionStyle = {
  overflowY: "hidden",
  overflowX: "hidden",
  height: "100%",
  width: "auto",
  backgroundImage: `url(${Background})`,
};

export class TrademarkForm3 extends Component {
  continue = e => {
    e.preventDefault();
    console.log(this.props);
    this.props.nextStep();
  };

  back = e => {
    e.preventDefault();
    this.props.prevStep();
  };

  state = {
    checked: false
  };

  onChange(e) {
    this.setState({ checked: e.target.checked });
  }

  render() {
    //const { values } = this.props;
    const { values, handleChange } = this.props;
    const { classes } = this.props;
    console.log("page 4 started")
    return (
      <React.Fragment>
        <section style={sectionStyle}>
          <div className="applicationFormTitle">
            <h1>4. International Information</h1>
          </div>
          <div className="formLayout">
            <div className="trademarkType">
              <h3>Have you filed or applied for this trademark in any other country?</h3>
              <br />
              <h3>

                <RadioGroup aria-label="userType" id="userType" name="userType" onChange={handleChange('filedInOtherCountry')}>
                  <FormControlLabel value="Yes" control={<Radio />} label="Yes" />
                  <FormControlLabel value="No" control={<Radio />} label="No" />
                </RadioGroup>

                {this.props.values.filedInOtherCountry == "Yes" &&
                  <p style={{ fontSize: "12pt", margin: "12px 0 -16px 0", }}>Please fill out information (if known):</p>
                }

                {this.props.values.filedInOtherCountry == 'Yes' &&
                  <TextField
                    id="countryOfFiling"
                    placeholder="Country of Filing"
                    label="Country of Filing"
                    margin="normal"
                    inputProps={{ maxLength: 50 }}
                    fullWidth={true}
                    onChange={handleChange('countryOfFiling')}
                    defaultValue={values.countryOfFiling}
                  />
                }

                {this.props.values.filedInOtherCountry == 'Yes' &&
                  <TextField
                    id="filingDate"
                    label="Filing Date"
                    InputLabelProps={{
                      shrink: true,
                    }}
                    type="date"
                    margin="normal"
                    fullWidth={true}
                    onChange={handleChange('filingDate')}
                    defaultValue={values.filingDate}
                  />
                }
                {this.props.values.filedInOtherCountry == 'Yes' &&
                  <TextField
                    id="applicationNo"
                    placeholder="Application/File number"
                    label="Application/File number"
                    margin="normal"
                    inputProps={{ maxLength: 60 }}
                    fullWidth={true}
                    onChange={handleChange('applicationNo')}
                    defaultValue={values.applicationNo}
                  />
                }
              </h3>
              <br />
              <br />
            </div>

            <Button style={{ margin: "4px" }} color="secondary" variant="contained" onClick={this.back}>Back</Button>
            <Button style={{ margin: "4px" }} color="primary" variant="contained" onClick={this.continue}>Continue</Button>
          </div>
        </section>
      </React.Fragment>
    );
  }
}

export default TrademarkForm3
