import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import MaterialTable from 'material-table';
import Typography from '@material-ui/core/Typography';
import { List, ListItem, ListItemText } from '@material-ui/core';
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent'
import Background from '../images/back_pattern.png'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

var sectionStyle = {
  overflowY: "hidden",
  overflowX: "hidden",
  height: "100%",
  width: "auto",
  backgroundImage: `url(${Background})`,
};

export class TrademarkForm2 extends Component {
  constructor(props) {
    super(props);
    console.log(props);
    this.clickContinue = this.clickContinue.bind(this);

    this.state = {
      terms: [],
      classShortNames: [],
      selectedClasses: [],
      selectedTerms: [],
      searchTerm: "",
      searchError: "",
      open: false
    };

  }

  componentDidMount() {
    if (this.props.values.classes.length > 0) {
      this.setState({
        selectedClasses: this.props.values.classes.slice()
      })
    }
    if (this.props.values.terms.length > 0) {
      this.setState({
        selectedTerms: this.props.values.terms.slice()
      })
    }
    this.fetchClassShortName();
  }

  clickContinue = e => {
    if (this.state.selectedTerms.length == 0) {
      this.setState({ open: true });
    } else {
      this.continue(e);
    }
  }

  continue = e => {
    e.preventDefault();
    this.props.handler('classes', this.state.selectedClasses);
    this.props.handler('terms', this.state.selectedTerms);
    this.changeAmount()
    console.log(this.state);
    this.props.nextStep();
  };

  back = e => {
    e.preventDefault();
    this.props.prevStep();
  };

  changeAmount() {
    var newAmount = 150000;
    if (this.state.selectedClasses.length > 1) {
      newAmount += ((this.state.selectedClasses.length - 1) * 100) * 100;
    }
    this.props.handler('amount', newAmount);
  }

  // checkTerms() {
  //   if (this.state.selectedClasses.length == 0) {
  //     alert("You must select AT LEAST one trademark term to continue.");
  //     return false;
  //   }
  //   return true;
  // }

  handleClose = () => {
    this.setState({ open: false });
  };

  validateSearchString() {
    let searchError = "";
    if (!this.state.searchTerm) {
      searchError = "Please enter a word that describes your trademark";
    }
    if (this.state.searchTerm.includes('/')) {
      searchError = "Your search cannot include '/'";
    }
    if (this.state.searchTerm.includes('.') || this.state.searchTerm.includes(';')
      || this.state.searchTerm.includes('$') || this.state.searchTerm.includes(',')
      || this.state.searchTerm.includes(':') || this.state.searchTerm.includes('?')
      || this.state.searchTerm.includes('@') || this.state.searchTerm.includes('#')
      || this.state.searchTerm.includes('!') || this.state.searchTerm.includes('&')
      || this.state.searchTerm.includes('*') || this.state.searchTerm.includes('\(')
      || this.state.searchTerm.includes('\'') || this.state.searchTerm.includes('\)')
      || this.state.searchTerm.includes('\"') || this.state.searchTerm.includes('\}')
      || this.state.searchTerm.includes('-') || this.state.searchTerm.includes('\{')
      || this.state.searchTerm.includes('%')) {
      searchError = "Please do not include any special characters"
    }
    if (searchError) {
      this.setState({ searchError });
      return false;
    } else {
      this.setState({ searchError });
    }
    return true;
  };

  getSearchTerms = () => {
    console.log(this.state);
    var searchString = this.state.searchTerm;
    if (this.validateSearchString()) {
      let url = 'https://localhost:5001/api/cipo/GetTermDataByString/' + searchString;
      console.log(url);
      fetch(url)
        .then(response => response.json())
        .then(data => {
          console.log(data);
          this.setState({
            terms: data.terms,
          })
        });
    }
  }

  handleTextFieldChange = (event) => {
    this.setState({
      searchTerm: event.target.value
    });
  }

  isDupTerm(termList, term) {
    if (termList.length == 0) {
      return false;
    } else {
      var i;
      var x;
      for (i = 0; i < termList.length; i++) {
        for (x = 0; x < termList[i].length; x++) {
          if (termList[i][x] == term) {
            return true;
          }
        }
      }
    }
    return false;
  }

  //Checks for any duplicate classes. If true, returns its index, else returns -1
  checkDupClass(classList, newClassNum) {
    var i;
    for (i = 0; i < classList.length; i++) {
      if (classList[i] == newClassNum) {
        return i;
      }
    }
    return -1;
  }

  async fetchClassShortName() {
    let tempShortNames = [];
    let url = 'https://localhost:5001/api/cipo/GetAllClasses/';
    await fetch(url)
      .then(response => response.json())
      .then(data => {
        var i
        for (i = 0; i < data.classes.length; i++) {

          tempShortNames.push(data.classes[i].name);
        }
      });
    this.setState({
      classShortNames: tempShortNames
    })
  }

  getClassShortName(index) {
    return this.state.classShortNames[index - 1];
  }


  handleAdd(data) {
    var tempClassList = this.state.selectedClasses.slice();//Slice returns a copy of array rather than a reference
    var tempTermList = this.state.selectedTerms.slice();

    data.forEach(element => {

      var classIndex = this.checkDupClass(tempClassList, element.termClass);
      if (classIndex == -1) {
        tempClassList.push(element.termClass)
        tempTermList.push(Array(element.termName));
      } else {
        if (!this.isDupTerm(tempTermList, element.termName)) {
          tempTermList[classIndex].push(element.termName);
        }
      }
    });

    this.setState({
      selectedClasses: tempClassList,
      selectedTerms: tempTermList,
    });
  }

  handleRemove(classNumber, term) {
    var tempClassList = this.state.selectedClasses.slice();
    var tempTermList = this.state.selectedTerms.slice();
    var classIndex = this.state.selectedClasses.indexOf(classNumber);

    console.log(classIndex);
    for (var i = 0; i < tempTermList[classIndex].length; i++) {
      if (tempTermList[classIndex][i].localeCompare(term) == 0) {
        tempTermList[classIndex].splice(i, 1);
      }
    }

    if (tempTermList[classIndex].length == 0) {
      tempTermList.splice(classIndex, 1);
      tempClassList.splice(classIndex, 1);
    }

    this.setState({
      selectedClasses: tempClassList,
      selectedTerms: tempTermList,
    });

  }

  render() {
    let amountText = "$" + 1500
    let additionalNICE = ""

    if (this.state.selectedClasses.length > 1) {
      amountText = "$" + String(1500 + ((this.state.selectedClasses.length - 1) * 100))
      additionalNICE = "$1500 base fee + " + String(this.state.selectedClasses.length - 1) + " additional NICE Classes."
    }
    return (
      <>
        <section style={sectionStyle}>
          <div className="applicationFormTitle">
            <h1>3.  Goods and Services</h1>
            <Typography gutterBottom>
              Please select the NICE Class and Terms you want to register your trademark under.
              <br />
              <p><b>Please Note:</b> You are allowed selections from 1 NICE Class, any additional NICE Classes will cost an additional $100.</p>
            </Typography>
          </div>
          <div className="formLayout">

            <div className="trademarkType">
              <h3>Search for your Trademark Terms</h3>
              <TextField
                id="outlined-basic"
                placeholder="Enter a general term for your good/service "
                label="Search"
                fullWidth
                variant="outlined"
                error={this.state.searchError != ""}
                helperText={this.state.searchError}
                value={this.searchTerm}
                onChange={this.handleTextFieldChange} />
              <Button color="primary" variant="contained" onClick={this.getSearchTerms}>Search</Button>

              <br /><br />

              <MaterialTable
                title="Terms List"
                columns={[
                  {
                    title: 'Term ID', field: 'id', cellStyle: {

                      minWidth: 10,
                      padding: 10
                    },
                    headerStyle: {
                      minWidth: 10,
                      padding: 10
                    },
                  },
                  { title: 'Term Name', field: 'termName' },
                  { title: 'NICE Class', field: 'termClass' },
                  { title: 'NICE Class Description', field: 'classShortName' }
                ]}
                data={this.state.terms}

                options={{
                  selection: true,
                  showSelectAllCheckbox: false
                }}
                actions={[
                  {
                    tooltip: 'Add All Selected Terms/Classes',
                    icon: () => <Button variant="contained" component="label" color="primary">Add selected items</Button>,
                    onClick: (evt, data) => this.handleAdd(data)
                  }
                ]}
              />

              <List>
                {this.state.selectedClasses.map((classNum) => (
                  <div>
                    <h4>{"Class: " + classNum + " - " + this.getClassShortName(classNum)}</h4>
                    <ListItem>
                      {this.state.selectedTerms[this.state.selectedClasses.indexOf(classNum)].map((term) => (
                        <div>
                          <Container maxWidth="lg">
                            <ListItemText primary={"Term:"} secondary={term} />
                            <Button color="secondary" variant="contained" onClick={() => this.handleRemove(classNum, term)}>Remove</Button>
                          </Container>
                        </div>
                      ))}
                    </ListItem>
                  </div>
                ))}
              </List>

              <Card >
                <CardContent>
                  <Typography variant="h5" component="h2">
                    <b>Amount:</b> {amountText}
                  </Typography>
                  <Typography variant="body1" component="p">
                    {additionalNICE}
                  </Typography>
                </CardContent>
              </Card>

              <br /><br />
              <Button style={{ margin: "4px" }} color="secondary" variant="contained" onClick={this.back}>Back</Button>
              <Button style={{ margin: "4px" }} color="primary" variant="contained" onClick={this.clickContinue}>Continue</Button>
            </div>
            <Dialog
              open={this.state.open}
              onClose={this.handleClose}
              aria-labelledby="alert-dialog-title"
              aria-describedby="alert-dialog-description"
            >
              <DialogTitle id="alert-dialog-title">{"Continue without selecting any terms?"}</DialogTitle>
              <DialogContent>
                <DialogContentText id="alert-dialog-description">
                  You have not selected any trademark terms. This can be figured out at a later time. Do you wish to continue?
                </DialogContentText>
              </DialogContent>
              <DialogActions>
                <Button color="secondary" variant="contained" onClick={this.handleClose}>
                  Back
                </Button>
                <Button color="primary" variant="contained" onClick={this.continue} autoFocus>
                  Continue
                </Button>
              </DialogActions>
            </Dialog>
          </div>
        </section>
      </>
    );
  }
}

export default TrademarkForm2
