import React, { Component } from 'react';
import Button from '@material-ui/core/Button';
import { Router, Link } from 'react-router-dom';
import Background from '../images/back_pattern.png'

var sectionStyle = {
  overflowY: "hidden",
  overflowX: "hidden",
  height: "100%",
  width: "auto",
  backgroundImage: `url(${Background})`,
};

export class Success extends Component {
  continue = e => {
    e.preventDefault();
    this.props.nextStep();
  };

  back = e => {
    e.preventDefault();
    this.props.prevStep();
  };

  render() {
    return (
      <React.Fragment>
        <section style={sectionStyle}>
          <div className="formLayout">
            <h1><b>Thank You For Your Submission!</b></h1>
            <h3>Your Trademark Registration request has been sent. You will receive an email within 2-3 business days regarding your request.</h3>
            <br />
            <Link to='/home' >
              <Button color="primary" variant="contained"> Return to Home page</Button>
            </Link>
          </div>
        </section>
      </React.Fragment>
    );
  }
}

export default Success;
