import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Checkbox from '@material-ui/core/Checkbox';
import FileInput from './FileInput';
import Background from '../images/back_pattern.png'

var sectionStyle = {
  overflowY: "hidden",
  overflowX: "hidden",
  height: "100%",
  width: "auto",
  backgroundImage: `url(${Background})`,
};

export class TrademarkForm extends Component {
  constructor(props) {
    super(props);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handler = props.handler.bind(this)


    this.state = {
      optionList: [],
      file: null
    };

    var option = {
      id: 1,
      name: "Standard Characters",
      checked: false
    }
    this.state.optionList.push(option);

    option = {
      id: 2,
      name: "Design/Logo",
      checked: false
    }
    this.state.optionList.push(option);

    option = {
      id: 3,
      name: "Others",
      checked: false
    }
    this.state.optionList.push(option);

    console.log(this.state);
  }

  handleInputChange(event) {
    console.log("value:" + event.target.value + " checked: " + event.target.checked);
    const { show } = this.state;
    this.setState({ show: !show })

    //find option with that id and set checked value to it
    for (const each of this.state.optionList) {
      if (each.id == event.target.value) {
        each.checked = event.target.checked;
      }
    }
    console.log(this.state);
  }

  continue = e => {
    e.preventDefault();
    console.log(this.props.values);
    this.props.handleArray(this.state.optionList);
    console.log(this.state);
    this.props.nextStep();
  };

  back = e => {
    e.preventDefault();
    this.props.prevStep();
  };

  onChange(e) {
    this.setState({ checked: e.target.checked });
  }

  componentDidMount() {
    var tempOptionList = this.state.optionList.slice();
    for (var i = 0; i < this.props.values.trademarkType.length; i++) {

      if (this.props.values.trademarkType[i].localeCompare("Standard Characters") == 0) {
        tempOptionList[0].checked = true;

      } else if (this.props.values.trademarkType[i].localeCompare("Design/Logo") == 0) {
        tempOptionList[1].checked = true;

      } else if (this.props.values.trademarkType[i].localeCompare("Others") == 0) {
        tempOptionList[2].checked = true;

      }
    }
    this.setState({
      optionList: tempOptionList
    })
  }

  render() {
    const { values, handleChange } = this.props;
    const { classes } = this.props;

    return (
      <React.Fragment>
        <section style={sectionStyle}>
          <div className="applicationFormTitle">
            <h1>2. Trademark Type</h1>
          </div>
          <div className="formLayout">
            <div className="trademarkType">
              <h3>Please select all that apply, and provide additional information if you are able:</h3>
              <br />

              <h4>
                <Checkbox value={this.state.optionList[0].id} checked={this.state.optionList[0].checked} onChange={this.handleInputChange} />
                        Standard characters

              {this.state.optionList[0].checked &&
                  <TextField
                    placeholder="Type out your trademark"
                    label="Type out your trademark below (letters, numbers, and/or symbols only)"
                    margin="normal"
                    fullWidth={true}
                    id="characterText"
                    inputProps={{ maxLength: 250 }}
                    onChange={handleChange('characterText')}
                    defaultValue={values.characterText}
                  />
                }
              </h4>
              <br />
              <br />

              <h4>
                <Checkbox value={this.state.optionList[1].id} checked={this.state.optionList[1].checked} onChange={this.handleInputChange} />
                Design or Logo
                <br />

                {this.state.optionList[1].checked &&
                  <div>
                    {/* <Button variant="contained" component="label" color="primary" onChange={this.handleUploadImage}>Upload File
                    <input type="file" style={{ display: "none" }} />
                  </Button> */}
                    <FileInput values={this.props} oldState={this.state} handler={this.handler} />
                    <h5>Preview: </h5>
                    <img className="prevImage" src={this.state.file} />
                  </div>
                }
              </h4>
              <br />
              <br />

              <h4>
                <Checkbox value={this.state.optionList[2].id} label="Others" checked={this.state.optionList[2].checked} onChange={this.handleInputChange} />
                        Others (Packaging, Texture, Smell, etc.)
            </h4>
            </div>
              <Button style={{ margin: "4px" }} color="secondary" variant="contained" onClick={this.back}>Back</Button>
              <Button style={{ margin: "4px" }} color="primary" variant="contained" onClick={this.continue}>Continue</Button>
          </div>
        </section>
      </React.Fragment>
    );
  }
}

export default TrademarkForm
