import React, { Component } from 'react';

import Confirm from '../components/Confirm';
import Success from '../components/Success';
import ApplicationForm from '../components/ApplicationForm';
import TrademarkForm from '../components/TrademarkForm';
import TrademarkForm2 from '../components/TrademarkForm2';
import TrademarkForm3 from '../components/TrademarkForm3';
import PaymentForm from '../components/PaymentForm'
import PaymentConfirmation from '../components/PaymentConfirmation'

import '../ApplicationForm.css';

export class Form extends Component {
    constructor(props) {
        super(props)
        //DELETE LATER
        console.log("Started page 1");
        this.handler = this.handler.bind(this)
        this.handleArray = this.handleArray.bind(this);

    }

    state = {
        step: 1,
        firstName: '',
        lastName: '',
        organizationName: '',

        streetAdress: '',
        city: '',
        province: '',
        country: '',
        postalCode: '',
        email: '',
        phone: '',
        fax: '',
        amount: 150000,
        id: "",

        userType: '',
        trademarkType: [],
        characterText: '',
        fileName: '',
        classes: [],
        terms: [],
        filedInOtherCountry: '',
        countryOfFiling: '',
        filingDate: '',
        applicationNo: ''
    };

    onUnload(event) {
        alert('page Refreshed')
    }

    componentDidMount() {
        window.onbeforeunload = function () {
            this.onUnload();
            return "";
        }.bind(this);

        window.history.pushState(null, null, window.location.pathname);
        window.addEventListener('popstate', this.onBackButtonEvent);
    }

    onBackButtonEvent = (e) => {
        e.preventDefault();
        if (!this.isBackButtonClicked) {

            if (window.confirm("You will be returned to the home page, your changes will not be saved. Are you sure?")) {
                this.isBackButtonClicked = true;
                window.history.back();
            } else {
                window.history.pushState(null, null, window.location.pathname);
                this.isBackButtonClicked = false;
            }
        }
    }

    componentWillUnmount() {
        window.removeEventListener("beforeunload", this.onUnload)
        window.removeEventListener('popstate', this.onBackButtonEvent);

    }

    nextStep = () => {
        const { step } = this.state;
        this.setState({
            step: step + 1
        });
        window.scrollTo(0, 0);
    };

    prevStep = () => {
        const { step } = this.state;
        this.setState({
            step: step - 1
        });
        window.scrollTo(0, 0);
    };

    handleChange = input => e => {
        this.setState({ [input]: e.target.value });
    };

    handleArray(val) {
        var temp = [];
        console.log(val);
        for (const each of val) {
            if (each.checked) {
                temp.push(each.name);
            }
            this.setState({
                trademarkType: temp
            });

        }
    }

    handler(name, value) {
        this.setState({
            [name]: value
        })
    }



    render() {
        const { step } = this.state;
        const { firstName, lastName, organizationName, streetAdress, city,
            province, country, postalCode, email, phone, fax, amount, id, userType,
            trademarkType, characterText, fileName, classes, terms, filedInOtherCountry, countryOfFiling, filingDate, applicationNo } = this.state;
        const values = {
            firstName, lastName, organizationName, streetAdress, city,
            province, country, postalCode, email, phone, fax, amount, id, userType,
            trademarkType, characterText, fileName, classes, terms, filedInOtherCountry, countryOfFiling, filingDate, applicationNo
        };

        switch (step) {
            case 1:
                return (
                    <ApplicationForm
                        nextStep={this.nextStep}
                        handleChange={this.handleChange}
                        values={values}
                    />
                );
            case 2:
                return (
                    <TrademarkForm
                        nextStep={this.nextStep}
                        prevStep={this.prevStep}
                        handleChange={this.handleChange}
                        values={values}
                        handler={this.handler}
                        handleArray={this.handleArray}
                    />
                );
            case 3:
                return (
                    <TrademarkForm2
                        nextStep={this.nextStep}
                        prevStep={this.prevStep}
                        handleChange={this.handleChange}
                        handler={this.handler}
                        values={values}
                    />
                );
            case 4:
                return (
                    <TrademarkForm3
                        nextStep={this.nextStep}
                        prevStep={this.prevStep}
                        handleChange={this.handleChange}
                        values={values}
                    />
                );
            case 5:
                return (
                    <Confirm
                        nextStep={this.nextStep}
                        prevStep={this.prevStep}
                        values={values}
                    />
                );
            case 6:
                return (
                    <PaymentForm
                        nextStep={this.nextStep}
                        prevStep={this.prevStep}
                        handleChange={this.handleChange}
                        values={values}
                        handler={this.handler}
                    />
                );
            case 7:
                return (
                    <PaymentConfirmation
                        nextStep={this.nextStep}
                        prevStep={this.prevStep}
                        handleChange={this.handleChange}
                        values={values} />
                );
            case 8:
                return <Success />;
        }
    }
}

export default Form;
