import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import $ from 'jquery';
import Button from '@material-ui/core/Button';

export default class FileInput extends Component {
  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleUploadImage = this.handleUploadImage.bind(this);
    this.fileInput = React.createRef();
    console.log(props);

    this.state = {
      fileName: "",
      uploadedFileName: "",
      errorMessage: ""
    }
  }

  componentDidMount() {
    console.log("test")
    if (this.state.fileName) {
      this.setState({
        uploadedFileName: this.props.values.values.fileName,
      })
    } else {
      this.setState({
        fileName: this.props.values.values.fileName,
      })
    }
    //console.log(this.props.values.values.fileName)
  }

  handleUploadImage(event) {
    if (this.checkFileSize(event)) {
      this.setState({
        errorMessage: ""
      })
      this.props.oldState.file = URL.createObjectURL(event.target.files[0]);
      return true;
    }
    return false;
  }

  checkFileSize = (event) => {
    let files = event.target.files[0];
    let size = 2000000;
    let err = "";
    if(files == null){
      return false;
    }
    if (files.size > size) {
      err += files.name + ' is too large, please pick a smaller file\n';
    }
    if (err !== '') {
      event.target.value = null
      console.log(err)
      this.setState(
        { errorMessage: err }
      )
      return false
    }
    return true;
  }


  async handleSubmit(event) {
    event.preventDefault();

    if(this.handleUploadImage(event)){

    

    console.log(
      `Selected file - ${this.fileInput.current.files[0].name}`
    );

    const formData = new FormData();

    formData.append('FileToUpload', this.fileInput.current.files[0]);

    console.log(formData.get('FileToUpload'));

    const options = {
      method: 'POST',
      body: formData
    };

    const response = await fetch('https://localhost:5001/api/files', options)
      .then(res => {
        console.log("file upload status");
        console.log(res.status);

        console.log(res);

        return res.json();
      })
      .then(
        data => {
          console.log("the filename is:");
          console.log(data.filename);
  
          console.log("data response");
          console.log(data);

          this.props.handler('fileName', data.filename);
          console.log("this state's filename:");
          console.log(this.state.fileName);
          
          console.log("the filename is:");
          console.log(data.filename);
          
          this.setState({
            fileName: data.filename
          });
        });
      }
  }

  render() {
    return (
      <form>
        <p>Select File to Upload: (image file under 2MB)</p>

        <Button variant="contained" component="label" color="primary" onChange={this.handleSubmit}>
          Choose File
        <input encType="multipart/form-data" accept="image/* " type="file" ref={this.fileInput} style={{ display: "none" }} />
        </Button>
        {this.state.fileName &&
          <p>Currently using {this.state.fileName}</p>
        }
        {this.state.errorMessage &&
          <p className="errorMessage">{this.state.errorMessage}</p>
        }
        <p>{this.state.uploadedFileName}</p>
        <p className="finePrint">If you wish to use a larger file, please follow 
        up with the confirmation email sent at the end of the form.</p>

      </form>
    );
  }
}