import React, { Component } from 'react';
import { List, ListItem, ListItemText } from '@material-ui/core/';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Background from '../images/back_pattern.png'

var sectionStyle = {
  overflowY: "hidden",
  overflowX: "hidden",
  height: "100%",
  width: "auto",
  backgroundImage: `url(${Background})`,
};

export class Confirm extends Component {
  continue = e => {
    e.preventDefault();
    this.props.nextStep();
    window.scrollTo(0, 0);
  };

  back = e => {
    e.preventDefault();
    this.props.prevStep();
    window.scrollTo(0, 0);
  };

  render() {
    const {
      values: { firstName, lastName, organizationName, streetAdress, city,
        province, country, postalCode, email, phone, fax, amount, id, userType,
        trademarkType, characterText, fileName, classes, terms, filedInOtherCountry, countryOfFiling, filingDate, applicationNo }
    } = this.props;
    return (
      <React.Fragment>
        <section style={sectionStyle}>
          <div className="formLayout">
            <h1>Confirm User Data</h1>
            <br />
            <List>
              <Typography variant="h6">
                First Name
            </Typography>
              <ListItem>
                <ListItemText primary={firstName} />
              </ListItem>
              <Typography variant="h6">
                Last Name
            </Typography>
              <ListItem>
                <ListItemText primary={lastName} />
              </ListItem>

              {this.props.values.userType == 'organization' &&
                <div>
                  <Typography variant="h6">
                    Organization Name
                </Typography>
                  <ListItem>
                    <ListItemText primary={organizationName} />
                  </ListItem>
                </div>
              }
              <Typography variant="h6">
                Street Address
            </Typography>
              <ListItem>
                <ListItemText primary={streetAdress} />
              </ListItem>
              
              <Typography variant="h6">
              Postal Code:
            </Typography>
              <ListItem>
                <ListItemText primary={postalCode} />
              </ListItem>

              <Typography variant="h6">
                City
            </Typography>
              <ListItem>
                <ListItemText primary={city} />
              </ListItem>

              <Typography variant="h6">
                Province
            </Typography>
              <ListItem>
                <ListItemText primary={province} />
              </ListItem>

              <Typography variant="h6">
                Country
            </Typography>
              <ListItem>
                <ListItemText primary={country} />
              </ListItem>

            <Typography variant="h6">
              Trademark Types:
            </Typography>
            {this.props.values.trademarkType.length == 0 &&
              <ListItem>
                <ListItemText primary={" "} />
              </ListItem>
            }
            {trademarkType.map((value) => (
              <ListItem>
                <ListItemText primary={value} secondary={value.localeCompare("Standard Characters") == 0 && characterText || value.localeCompare("Design/Logo") == 0 && fileName}/>
              </ListItem>
            ))}

            

              <Typography variant="h6">
                Email
            </Typography>
              <ListItem>
                <ListItemText primary={email} />
              </ListItem>

            <Typography variant="h6">
              Phone
            </Typography>
              <ListItem>
                <ListItemText primary={phone} />
              </ListItem>

              <Typography variant="h6">
                Fax
            </Typography>
              <ListItem>
                <ListItemText primary={fax} />
              </ListItem>

              <Typography variant="h6">
                Classes Selected
            </Typography>

              {this.props.values.classes.length == 0 &&
                <ListItem>
                  <ListItemText primary={" "} />
                </ListItem>
              }
              {classes.map((value) => (
                <ListItem>
                  <ListItemText primary={"Class: " + value} />
                </ListItem>
              ))}

              <Typography variant="h6">
                Terms Selected
            </Typography>

              {this.props.values.terms.length == 0 &&
                <ListItem>
                  <ListItemText primary={" "} />
                </ListItem>
              }
              {terms.map((term) => (
                <ListItem>
                  <ListItemText primary={term || " "} />
                </ListItem>

              ))}
              <Typography variant="h6">
                Filed in other country
            </Typography>
              <ListItem>
                <ListItemText primary={filedInOtherCountry} />
              </ListItem>

              {this.props.values.filedInOtherCountry == 'Yes' &&
                <div>

                  <Typography variant="h6">
                    Country of Filing
                </Typography>
                  <ListItem>
                    <ListItemText primary={countryOfFiling} />
                  </ListItem>
                </div>

              }
              {this.props.values.filedInOtherCountry == 'Yes' &&
                <div>

                  <Typography variant="h6">
                    Filing Date
                </Typography>
                  <ListItem>
                    <ListItemText primary={filingDate} />
                  </ListItem>
                </div>

              }
              {this.props.values.filedInOtherCountry == 'Yes' &&
                <div>

                  <Typography variant="h6">
                    Application/File Number
                </Typography>
                  <ListItem>
                    <ListItemText primary={applicationNo} />
                  </ListItem>
                </div>

              }
            </List>

            <br />
            <br />
            <br />

            <Button style={{ margin: "4px" }} color="secondary" variant="contained" onClick={this.back}>Back</Button>
            <Button style={{ margin: "4px" }} color="primary" variant="contained" onClick={this.continue}>Confirm and Go To Payment</Button>
          </div>
        </section>
      </React.Fragment>
    );
  }
}

export default Confirm;
