import React, { Component } from 'react';
import { List, ListItem, ListItemText } from '@material-ui/core/';
import Button from '@material-ui/core/Button';
import Background from '../images/back_pattern.png';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

var sectionStyle = {
  overflowY: "hidden",
  overflowX: "hidden",
  height: "100%",
  width: "auto",
  backgroundImage: `url(${Background})`,
};

export class PaymentConfirmation extends Component {
  constructor(props) {
    super(props);
    console.log(this.props.values.amount);
    console.log(this.props.values.id);
    this.state = {
      matterId: '',
      disableButton: false,
      buttonText: 'Confirm and Check Out',
      dialogOpen: false
    }
  }

  continue = e => {
    e.preventDefault();
    this.props.nextStep();
  };

  back = e => {
    e.preventDefault();
    this.props.prevStep();
  };

  async createClioContact() {
    var name = this.props.values.firstName + " " + this.props.values.lastName;
    var type = "Person";

    var fnResponse = false;

    await fetch('https://localhost:5001/api/Clio', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        data: {
          "name": name,
          "type": type,
          "email_addresses": [{
            "name": "Other",
            "address": this.props.values.email
          }],
          "phone_numbers": [{
            "name": "Other",
            "number": this.props.values.phone
          },
          {
            "name": "Fax",
            "number": this.props.values.fax
          }],
          "addresses": [{
            "name": "Other",
            "street": this.props.values.streetAdress,
            "city": this.props.values.city,
            "province": this.props.values.province,
            "postal_code": this.props.values.postalCode,
            "country": this.props.values.country,
            "primary": true
          }]
        }
      })
    })
      .then(res => {
        console.log("contact create status");
        console.log(res.status);
        if (res.status != 200) {
          fnResponse = false;
        }
        return res.json();
    })
      .then(data => {

        var matterIdd = data.data.display_number;

        console.log("the matter ID is:");
        console.log(matterIdd);

        console.log("matter create response");
        console.log(data);
        
        this.setState({
          matterId: matterIdd
        });

        fnResponse = true;

      })
      .catch(err => {
        console.log(err);
      });

      console.log("createContact() returning:");
      console.log(fnResponse);
      return fnResponse;
  }

  async createEmail() {
    var name = this.props.values.firstName + " " + this.props.values.lastName;
    var type;
    if (this.props.values.userType.localeCompare("individual") == 0) {
      type = "Person"
    } else {
      type = "Company"
    }

    const createdContact = await this.createClioContact();

    if (!createdContact) {
      console.log("createEmail(): contact was not created!");
      return false;
    }

    var fnResponse = false;

    console.log("The following are the prop values");
    console.log(this.props.values);

    await fetch('https://localhost:5001/api/Email', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        data: {
          "name": name,
          "type": type,
          "organizationName": this.props.values.organizationName,
          "amountPaid": (this.props.values.amount / 100),

          "contactInfo": {
            "emailAddress": this.props.values.email,
            "phoneNumber": this.props.values.phone,
            "fax": this.props.values.fax,
            "matterId": this.state.matterId
          },

          "addressInfo": {
            "street": this.props.values.streetAdress,
            "city": this.props.values.city,
            "province": this.props.values.province,
            "postal_code": this.props.values.postalCode,
            "country": this.props.values.country,
          },

          "trademarkInfo": {
            "trademarkType": this.props.values.trademarkType,
            "characterText": this.props.values.characterText,
            "fileName": this.props.values.fileName,
            // nice classes
            "classes": this.props.values.classes,
            "terms": this.props.values.terms
          },

          "prevTrademarkInfo": {
            "filedInOtherCountry": this.props.values.filedInOtherCountry,
            "countryOfFiling": this.props.values.countryOfFiling,
            "filingDate": this.props.values.filingDate,
            "applicationNum": this.props.values.applicationNo
          }
        }
      })
    }).then(res => {
      if (res.status == 200) {
        console.log("email sent!");
        fnResponse = true;
      } else if (res.status >= 400) {
        console.log("email not sent!");
        fnResponse = false;
      }
    }).catch(err => {
      console.log(err);
    });

    console.log("createEmail() returning:");
    console.log(fnResponse);
    return fnResponse;

    // end of email function

  }

  async sendPayment() {
    this.setState({
      disableButton:true,
      buttonText: 'Submitting...'
    })
    //console.log(this.state.id);
    fetch('https://localhost:5001/api/Payment', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        "amount": String(this.props.values.amount),
        "method": String(this.props.values.id),
        "account_id": "RYJwNJeOSGG8J8pHMuK5bw"//Change for production
      })
    }).then(async (res) => {
      console.log(res.body);
      if (res.status == 200) {
        
        const createdEmail = await this.createEmail();

        console.log("attempting next step!");
        if (createdEmail) {
          console.log("email was indeed created. let's go to the next step in our relationship");
          this.props.nextStep();
        } else {
          this.setState({
            dialogOpen: true,
            disableButton: false
          });
        }

      } else {
        alert("The payment did not go through. Please revise your payment information or go to the 'Contact Us' link to get in touch.");
        this.setState({
          disableButton: false,
          buttonText: 'Confirm and Check Out'
        });
      }
    }).catch(err => {
      
      alert("The form could not be submitted because of a technical issue. Please try again, or go to the 'Contact Us' link to get in touch.  "
        + err);
      this.setState({
        disableButton: false,
        buttonText: 'Confirm and Check Out'
      });

    });
  }

  handleCloseDialog = () => {
    this.setState({ dialogOpen: false });
  };



  render() {
    const {
      values: { firstName, lastName, organizationName, streetAdress, city,
        province, country, postalCode, email, phone, fax, amount, id }
    } = this.props;
    return (
      <React.Fragment>
        <section style={sectionStyle}>
          <div className="formLayout">
            <h1>Confirm Your Trademark Registration</h1>
            <br />
            <List>
              <ListItem>
                <ListItemText primary="First Name" secondary={firstName} />
              </ListItem>
              <ListItem>
                <ListItemText primary="Last Name" secondary={lastName} />
              </ListItem>
              {this.props.values.userType == 'organization' &&
                <ListItem>
                  <ListItemText primary="Organization Name" secondary={organizationName} />
                </ListItem>
              }
              <ListItem>
                <ListItemText primary="StreetAdress" secondary={streetAdress} />
              </ListItem>
              <ListItem>
                <ListItemText primary="City" secondary={city} />
              </ListItem>
              <ListItem>
                <ListItemText primary="Province" secondary={province} />
              </ListItem>
              <ListItem>
                <ListItemText primary="Country" secondary={country} />
              </ListItem>
              <ListItem>
                <ListItemText primary="PostalCode" secondary={postalCode} />
              </ListItem>
              <ListItem>
                <ListItemText primary="Email" secondary={email} />
              </ListItem>
              <ListItem>
                <ListItemText primary="Phone" secondary={phone} />
              </ListItem>
              <ListItem>
                <ListItemText primary="Fax" secondary={fax} />
              </ListItem>

              <ListItem>
                <ListItemText primary="Method" secondary={id} />
              </ListItem><ListItem>
                <ListItemText primary="Amount" secondary={"$"+amount/100+".00"} />
              </ListItem><ListItem>
                <ListItemText primary="Fax" secondary={fax} />
              </ListItem>
            </List>

            <br />
            <br />
            <br />

            <Button style={{ margin: "4px" }} color="secondary" variant="contained" onClick={this.back}>Back</Button>
            <Button style={{ margin: "4px" }} disabled={this.state.disableButton} color="primary" variant="contained" onClick={() => this.sendPayment()}>{this.state.buttonText}</Button>
          


            <Dialog
              open={this.state.dialogOpen}
              onClose={this.handleCloseDialog}
              aria-labelledby="alert-dialog-title"
              aria-describedby="alert-dialog-description"
            >
              <DialogTitle id="alert-dialog-title">{"Email Error"}</DialogTitle>
              <DialogContent>
                <DialogContentText id="alert-dialog-description">
                  Your payment request was successful, but the confirmation email 
                  could not be sent due to technical issues. Please get in touch with
                  Golbey Law through the 'Contact Us' link to follow up.
                </DialogContentText>
              </DialogContent>
              <DialogActions>
                {/* <Button color="secondary" variant="contained" onClick={this.handleClose}>
                  Back
                </Button> */}
                <Button color="primary" variant="contained" onClick={this.continue} autoFocus>
                  I Understand
                </Button>
              </DialogActions>
            </Dialog>
          
          </div>
        </section>
      </React.Fragment>
    );
  }
}

export default PaymentConfirmation;